import React, { Suspense, useRef, useState } from "react"
import { MathUtils, Mesh, RepeatWrapping, DoubleSide } from "three";

//@ts-ignore
import { TextureLoader } from 'three/src/loaders/TextureLoader';
//@ts-ignore
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';

import { Canvas, useFrame, useLoader } from "react-three-fiber"
import Architect from "../components/architect";
import "./canvas.css";

import patternOne from "../images/icon.png";
import fontRobotoRegular from "./Roboto_Regular.json";


function TextMesh(props: any) {
  const [hovered, setHover] = useState(false);
  const mesh: Mesh = useRef();

  // actions to perform in current frame
  useFrame(() => {
    mesh.current.rotation.x = mesh.current.rotation.y += 0.01;
    mesh.current.geometry.center();
  });

  // create lava texture
  // const lava_texture = useLoader(THREE.TextureLoader, lava);
  // lava_texture.wrapS = THREE.RepeatWrapping;
  // lava_texture.wrapT = THREE.RepeatWrapping;
  // lava_texture.repeat.set(0.05, 0.05);

  // // create texture
  const patternOneTexture = useLoader(TextureLoader, patternOne);
  patternOneTexture.wrapS = RepeatWrapping;
  patternOneTexture.wrapT = RepeatWrapping;
  patternOneTexture.repeat.set(1, 1);

  // load in font
  const font = new FontLoader().parse(fontRobotoRegular);

  // configure font mesh
  const textOptions = {
    font,
    size: 5,
    height: hovered ? 0 : 1
  };

  const getTexture = () => {
    const textureMap: any = {
      //lava: lava_texture,
      //carbonFiber: cf_texture,
      texture: patternOneTexture,
    };

    return textureMap[props.texture];
  };

  return (
    <mesh
      {...props}
      ref={mesh}
      onPointerOver={(e) => setHover(true)}
      onPointerOut={(e) => setHover(false)}
    >
      <textGeometry attach="geometry" args={[props.text, textOptions]} />
      <meshStandardMaterial attach="material" args={[{ map: getTexture() }]} />
    </mesh>
  );
}


const Box = (props: any) => {

  // This reference will give us direct access to the mesh so we can animate it
  const mesh: any = useRef();

  // Set up state for the hovered and active state
  const [hovered, setHover] = useState(false)
  const [active, setActive] = useState(false)

  // Rotate mesh every frame, this is outside of React without overhead
  useFrame((state, delta) => {
    console.log(state, delta);
    if (mesh.current && mesh.current.rotation) {
      mesh.current.rotation.x = mesh.current.rotation.y += 0.01
    }
  })

  return (
    <mesh
        {...props}
        ref={mesh}
        scale={active ? [1.5, 1.5, 1.5] : [1, 1, 1]}
        onClick={e => setActive(!active)}
        onPointerOver={e => setHover(true)}
        onPointerOut={e => setHover(false)}
    >
      <boxBufferGeometry attach="geometry" args={[1, 1, 1]} />
      <meshStandardMaterial
      attach="material"
      color={hovered ? "hotpink" : "orange"}
      />
    </mesh>
  )
};

